import React from 'react';
import PropTypes from 'prop-types';
import Button from 'material-ui/Button';
import Card, { CardActions, CardContent, CardMedia } from 'material-ui/Card';
import { navigateTo } from "gatsby-link"
import { withStyles } from 'material-ui/styles';
import withRoot from '../../withRoot';

const styles = theme => ({
  announcementCard: {
    margin: '0 auto',
    paddingBottom: 16,
    backgroundColor: '#F3F4F6',
  },
  header: {
    textAlign: 'left',
    color: '#38404F',
    fontSize: '1.75vw',
    fontWeight: 700,
    fontFamily: 'source sans pro',
    marginTop: 0,
  },
  contentContainer: {},
  actionsContainer: {
    padding: [[12, 24, 24, 24]],
  },
  cardDate: {
    fontSize: '0.8vw',
    fontFamily: 'source sans pro',
    color: 'grey',
  },
  cardTitle: {
    marginTop: 10,
    fontSize: '1.25vw',
    fontFamily: 'source sans pro',
  },
  cardContent: {
    fontFamily: 'source sans pro',
    fontSize: '1vw',
  },
  actionButton: {
    marginRight: 12,
  },
  greenButton: {
    backgroundColor: '#65D777',
    color: 'white',
  },
  media: {
    height: 0,
    paddingTop: '25%',
  },
});


class AnnouncementCard extends React.Component {
  render() {
    const { classes } = this.props;

    const buttons = [];
    if (this.props.button1text && this.props.button1link) {
      buttons.push(
        <Button className={[classes.actionButton, classes.greenButton, "main-class"].join(' ')} variant="raised" size="small" key="button1"
          onClick={() => 
            this.props.button1link.startsWith('/') ? navigateTo(this.props.button1link) : window.open(this.props.button1link, '_blank')
          }
        >
          {this.props.button1text}
        </Button>,
      );
    }

    if (this.props.button2text && this.props.button2link) {
      buttons.push(
        <Button className={classes.actionButton} variant="raised" size="small" key="button2"
          onClick={() =>
            this.props.button2link.startsWith('/') ? navigateTo(this.props.button2link) : window.open(this.props.button2link, '_blank')
          }
        >
          {this.props.button2text}
        </Button>,
      );
    }

    const image = [];
    if (this.props.imagePath) {
      image.push(
        <CardMedia image={this.props.imagePath} className={classes.media} key="image"/>
      );
    }

    return (
      <div className={classes.announcementCard}>
        <Card elevation={5}>
          {image}
          <CardContent className={classes.contentContainer}>
            <a className={classes.cardDate}>
              {this.props.datetime}
            </a>
            <h1 className={classes.cardTitle}>
              {this.props.title}
            </h1>
            <a className={classes.cardContent}>
              {this.props.content}
            </a>
          </CardContent>
          <CardActions className={classes.actionsContainer}>
            {buttons}
          </CardActions>
        </Card>

      </div>
    );
  }
}

AnnouncementCard.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withRoot(withStyles(styles)(AnnouncementCard));


