import React from 'react';
import PropTypes from 'prop-types';
import AnnouncementCard from '../AnnouncementCard/AnnouncementCard';
import { withStyles } from 'material-ui/styles';
import withRoot from '../../withRoot';

import dcChinaImgPath from '../../../public/img/dc-china.png';

const styles = theme => ({
  announcements: {
    width: '90%',
    margin: '0 auto',
    backgroundColor: '#F3F4F6',
  },
  header: {
    textAlign: 'left',
    color: '#38404F',
    fontSize: '1.75vw',
    fontWeight: 700,
    fontFamily: 'source sans pro',
    marginTop: 0,
  },
});


class Announcements extends React.Component {
  render() {
    const { classes } = this.props;
    return (
      <div className={classes.announcements}>
        <h1 className={classes.header}>Announcements</h1>

        <AnnouncementCard
          datetime="4:36 PM - 19 Mar 2018"
          title="Our CFP is open!"
          content="Our human CFP is live! Submissions due by June 15th. Embodied AGIs may apply later."
          button1text="Submit"
          button1link="https://docs.google.com/forms/d/e/1FAIpQLSfDzURLjTMoSvjWYDinEu9n1sNc5HOBGWjU5UAzL-aNSjkwjA/viewform?usp=send_form"
          button2text="Ask us a question"
          button2link="https://twitter.com/aivillage_dc/status/975878712028626946"
        />

        <AnnouncementCard
          datetime="3:57 PM - 12 Apr 2018"
          title="AI Village will be at DEF CON China (beta)"
          content="We will have 5 amazing talks and 1 workshop!
            See you in Beijing May 12-13 2018."
          button2text="View program"
          button2link="/china18"
          imagePath={dcChinaImgPath}
        />

      </div>
    );
  }
}

Announcements.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withRoot(withStyles(styles)(Announcements));


