import React from 'react';
import PropTypes from 'prop-types';
import TextField from 'material-ui/TextField';
import Button from 'material-ui/Button';
import grey from 'material-ui/colors/grey';
import MailchimpSubscribe from 'react-mailchimp-subscribe';

import { withStyles } from 'material-ui/styles';
import withRoot from '../../withRoot';

const mailchimpURL = "//aivillage.us18.list-manage.com/subscribe/post?u=86ba12dcb1c3d8f1ec9cf045e&id=566659721c";

const styles = theme => ({
  section: {
    width: '55%',
    margin: '12 auto',
    paddingBottom: '5%',
    backgroundColor: '#F3F4F6',
    display: 'flex',
    flexDirection: 'row',
    flexWrap: 'wrap',
  },
  inspiringMessage: {
    textAlign: 'center',
    flex: '0 1 100%',
    color: grey[600],
    fontSize: '1.5vw',
    fontWeight: 400,
    fontFamily: 'source sans pro',
    paddingBottom: '3%',
  },
  mailchimpContainer: {
    width: '60%',
  },
  signupForm: {
    background: "#efefef",
    borderRadius: 2,
    padding: 10,
    display: "inline-block",
    width: '40%',
    margin: '0 auto',
  },
  emailTextField: {
    float: 'left',
    width: '100%',
  },
  submitButton: {
    float: 'right',
    marginTop: 12,
    backgroundColor: '#65D777',
    color: 'white',
  },
  statusMessageBlue: {
    fontFamily:'source sans pro',
    color: 'blue',
    marginBottom: 8,
  },
  statusMessageRed: {
    fontFamily:'source sans pro',
    color: 'red',
    marginBottom: 8,
  },
  statusMessageGreen: {
    fontFamily:'source sans pro',
    color: 'green',
    marginBottom: 8,
  },
});

class MailingListSignup extends React.Component {
  render() {
    const { classes } = this.props;
    let email;
    return (
      <div className={classes.section}>
        <div className={classes.inspiringMessage}>
          Follow our journey, subscribe to our mailing list
        </div>

        <MailchimpSubscribe
          url={mailchimpURL}
          className={classes.mailchimpContainer}
          render={({ subscribe, status, message }) => (
            <div className={classes.signupForm}>
              {status === "sending" && <div className={classes.statusMessageBlue}>Sending...</div>}
              {status === "error" && <div className={classes.statusMessageRed}>{message}</div>}
              {status === "success" && <div className={classes.statusMessageGreen}>{message}</div>}
              <TextField
                className={classes.emailTextField}
                id="input-textfield"
                inputRef={node => (email = node)}
                type="email"
                label="E-mail"
              />
              <br />
              <Button variant="raised" className={classes.submitButton}
                onClick={() => 
                  email &&
                  email.value.indexOf('@') > -1 &&
                  subscribe({EMAIL:email.value})
                }
              >
                Submit
              </Button>
            </div>
          )}
        />
      </div>
    );
  }
}

MailingListSignup.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withRoot(withStyles(styles)(MailingListSignup));


